  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  display:inline-block;
  overflow: hidden;
}
#app {
  position:fixed;
  display:inline-grid;
  grid-template-columns: 200px 1fr;
  grid-template-rows: 50px 1fr 1fr;
  grid-template-areas:
    "header header "
    "seglist segview"
    "seglist selview";
  width:100%;
  height:100%;
  margin:0;
  padding:0;
}


